.post-subtitle {
  text-align: start !important;
  unicode-bidi: plaintext;
  margin-top: 0;
}

h2 {
  text-align: start !important;
}


.post-meta {
  margin: 0;
  font-size: 10px;
  text-align: right;
}

.no-title {
  font-size: 12px;
  font-style: italic;
}
